/* Footer Styles */
.app-footer {
  background: linear-gradient(135deg, rgba(15,23,42,0.95) 0%, rgba(30,41,59,0.92) 100%);
  backdrop-filter: blur(20px);
  border-top: 1px solid rgba(255,255,255,0.1);
  margin-top: 80px;
  padding: 40px 0 20px 0;
  color: #ffffff;
  position: relative;
  z-index: 10;
}

.footer-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.footer-main {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
  gap: 40px;
  margin-bottom: 40px;
}

.footer-section {
  display: flex;
  flex-direction: column;
}

.footer-title {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 16px;
  color: #ffffff;
  letter-spacing: 0.5px;
}

.footer-description {
  font-size: 1rem;
  line-height: 1.6;
  color: #cbd5e1;
  margin-bottom: 0;
  opacity: 0.9;
}

.footer-subtitle {
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 16px;
  color: #ffffff;
  letter-spacing: 0.3px;
}

/* Language Selector */
.language-selector {
  position: relative;
  display: inline-block;
}

.language-btn {
  background: rgba(255,255,255,0.1);
  border: 1px solid rgba(255,255,255,0.2);
  color: #ffffff;
  padding: 12px 20px;
  border-radius: 12px;
  cursor: pointer;
  font-size: 0.95rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.language-btn:hover {
  background: rgba(255,255,255,0.15);
  border-color: rgba(255,255,255,0.3);
  transform: translateY(-2px);
}

.language-btn.current {
  background: rgba(59,130,246,0.2);
  border-color: rgba(59,130,246,0.4);
}

.language-dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background: rgba(15,23,42,0.98);
  border: 1px solid rgba(255,255,255,0.2);
  border-radius: 12px;
  box-shadow: 0 8px 32px rgba(0,0,0,0.3);
  backdrop-filter: blur(20px);
  z-index: 1000;
  max-height: 300px;
  overflow-y: auto;
  opacity: 0;
  visibility: hidden;
  transform: translateY(-10px);
  transition: all 0.3s ease;
  margin-top: 8px;
}

.language-dropdown.show {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.language-option {
  background: none;
  border: none;
  color: #ffffff;
  padding: 12px 20px;
  width: 100%;
  text-align: left;
  cursor: pointer;
  font-size: 0.95rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: all 0.2s ease;
  border-bottom: 1px solid rgba(255,255,255,0.1);
}

.language-option:last-child {
  border-bottom: none;
}

.language-option:hover {
  background: rgba(255,255,255,0.1);
  color: #3b82f6;
}

/* Footer Links */
.footer-links {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.footer-link {
  color: #cbd5e1;
  text-decoration: none;
  font-size: 0.95rem;
  transition: all 0.2s ease;
  opacity: 0.8;
}

.footer-link:hover {
  color: #3b82f6;
  opacity: 1;
  transform: translateX(4px);
}

/* Footer Bottom */
.footer-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 30px;
  border-top: 1px solid rgba(255,255,255,0.1);
}

.footer-info {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.footer-text {
  font-size: 0.9rem;
  color: #cbd5e1;
  opacity: 0.8;
}

.footer-version {
  font-size: 0.8rem;
  color: #94a3b8;
  opacity: 0.6;
}

/* Social Links */
.footer-social {
  display: flex;
  gap: 16px;
}

.social-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background: rgba(255,255,255,0.1);
  border: 1px solid rgba(255,255,255,0.2);
  border-radius: 50%;
  color: #ffffff;
  text-decoration: none;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.social-link:hover {
  background: rgba(59,130,246,0.3);
  border-color: rgba(59,130,246,0.5);
  transform: translateY(-3px) scale(1.1);
  color: #3b82f6;
}

.social-icon {
  width: 20px;
  height: 20px;
}

/* Mobile Responsive */
@media (max-width: 768px) {
  .app-footer {
    margin-top: 60px;
    padding: 30px 0 20px 0;
  }
  
  .footer-container {
    padding: 0 16px;
  }
  
  .footer-main {
    grid-template-columns: 1fr;
    gap: 30px;
    margin-bottom: 30px;
  }
  
  .footer-section {
    text-align: center;
  }
  
  .footer-title {
    font-size: 1.3rem;
    margin-bottom: 12px;
  }
  
  .footer-description {
    font-size: 0.95rem;
  }
  
  .footer-subtitle {
    font-size: 1rem;
    margin-bottom: 12px;
  }
  
  .language-selector {
    display: flex;
    justify-content: center;
  }
  
  .language-btn {
    padding: 10px 16px;
    font-size: 0.9rem;
  }
  
  .language-dropdown {
    left: 50%;
    transform: translateX(-50%) translateY(-10px);
    min-width: 200px;
  }
  
  .language-dropdown.show {
    transform: translateX(-50%) translateY(0);
  }
  
  .footer-bottom {
    flex-direction: column;
    gap: 20px;
    text-align: center;
  }
  
  .footer-social {
    justify-content: center;
  }
  
  .footer-info {
    order: 2;
  }
  
  .footer-social {
    order: 1;
  }
  
  .social-link {
    width: 36px;
    height: 36px;
  }
  
  .social-icon {
    width: 18px;
    height: 18px;
  }
}

@media (max-width: 480px) {
  .app-footer {
    margin-top: 50px;
    padding: 25px 0 15px 0;
  }
  
  .footer-container {
    padding: 0 12px;
  }
  
  .footer-main {
    gap: 25px;
    margin-bottom: 25px;
  }
  
  .footer-title {
    font-size: 1.2rem;
  }
  
  .footer-description {
    font-size: 0.9rem;
  }
  
  .language-btn {
    padding: 8px 14px;
    font-size: 0.85rem;
  }
  
  .language-dropdown {
    min-width: 180px;
  }
  
  .footer-bottom {
    gap: 15px;
  }
  
  .footer-social {
    justify-content: center;
  }
  
  .social-link {
    width: 36px;
    height: 36px;
  }
  
  .social-icon {
    width: 18px;
    height: 18px;
  }
  
  .footer-text {
    font-size: 0.85rem;
  }
  
  .footer-version {
    font-size: 0.75rem;
  }
}

/* Tablet Styles */
@media (min-width: 769px) and (max-width: 1024px) {
  .footer-main {
    grid-template-columns: 1.5fr 1fr 1fr;
    gap: 30px;
  }
  
  .footer-container {
    padding: 0 24px;
  }
}

/* Dark mode enhancements */
@media (prefers-color-scheme: dark) {
  .app-footer {
    background: linear-gradient(135deg, rgba(10,14,20,0.98) 0%, rgba(20,28,36,0.95) 100%);
  }
  
  .language-dropdown {
    background: rgba(10,14,20,0.98);
  }
}

/* High contrast mode */
@media (prefers-contrast: high) {
  .app-footer {
    border-top: 2px solid #ffffff;
  }
  
  .footer-link:hover {
    text-decoration: underline;
  }
  
  .social-link {
    border: 2px solid #ffffff;
  }
}

/* Reduced motion */
@media (prefers-reduced-motion: reduce) {
  .language-btn,
  .language-option,
  .footer-link,
  .social-link {
    transition: none;
  }
  
  .language-dropdown {
    transition: opacity 0.2s ease;
  }
}
