.app-footer {
  backdrop-filter: blur(20px);
  color: #fff;
  z-index: 10;
  background: linear-gradient(135deg, #0f172af2 0%, #1e293beb 100%);
  border-top: 1px solid #ffffff1a;
  margin-top: 80px;
  padding: 40px 0 20px;
  position: relative;
}

.footer-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.footer-main {
  grid-template-columns: 2fr 1fr 1fr;
  gap: 40px;
  margin-bottom: 40px;
  display: grid;
}

.footer-section {
  flex-direction: column;
  display: flex;
}

.footer-title {
  color: #fff;
  letter-spacing: .5px;
  margin-bottom: 16px;
  font-size: 1.5rem;
  font-weight: 700;
}

.footer-description {
  color: #cbd5e1;
  opacity: .9;
  margin-bottom: 0;
  font-size: 1rem;
  line-height: 1.6;
}

.footer-subtitle {
  color: #fff;
  letter-spacing: .3px;
  margin-bottom: 16px;
  font-size: 1.1rem;
  font-weight: 600;
}

.language-selector {
  display: inline-block;
  position: relative;
}

.language-btn {
  color: #fff;
  cursor: pointer;
  backdrop-filter: blur(10px);
  background: #ffffff1a;
  border: 1px solid #fff3;
  border-radius: 12px;
  align-items: center;
  gap: 8px;
  padding: 12px 20px;
  font-size: .95rem;
  font-weight: 500;
  transition: all .3s;
  display: flex;
}

.language-btn:hover {
  background: #ffffff26;
  border-color: #ffffff4d;
  transform: translateY(-2px);
}

.language-btn.current {
  background: #3b82f633;
  border-color: #3b82f666;
}

.language-dropdown {
  backdrop-filter: blur(20px);
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  background: #0f172afa;
  border: 1px solid #fff3;
  border-radius: 12px;
  max-height: 300px;
  margin-top: 8px;
  transition: all .3s;
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  overflow-y: auto;
  transform: translateY(-10px);
  box-shadow: 0 8px 32px #0000004d;
}

.language-dropdown.show {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.language-option {
  color: #fff;
  text-align: left;
  cursor: pointer;
  background: none;
  border: none;
  border-bottom: 1px solid #ffffff1a;
  align-items: center;
  gap: 8px;
  width: 100%;
  padding: 12px 20px;
  font-size: .95rem;
  font-weight: 500;
  transition: all .2s;
  display: flex;
}

.language-option:last-child {
  border-bottom: none;
}

.language-option:hover {
  color: #3b82f6;
  background: #ffffff1a;
}

.footer-links {
  flex-direction: column;
  gap: 12px;
  display: flex;
}

.footer-link {
  color: #cbd5e1;
  opacity: .8;
  font-size: .95rem;
  text-decoration: none;
  transition: all .2s;
}

.footer-link:hover {
  color: #3b82f6;
  opacity: 1;
  transform: translateX(4px);
}

.footer-bottom {
  border-top: 1px solid #ffffff1a;
  justify-content: space-between;
  align-items: center;
  padding-top: 30px;
  display: flex;
}

.footer-info {
  flex-direction: column;
  gap: 8px;
  display: flex;
}

.footer-text {
  color: #cbd5e1;
  opacity: .8;
  font-size: .9rem;
}

.footer-version {
  color: #94a3b8;
  opacity: .6;
  font-size: .8rem;
}

.footer-social {
  gap: 16px;
  display: flex;
}

.social-link {
  color: #fff;
  backdrop-filter: blur(10px);
  background: #ffffff1a;
  border: 1px solid #fff3;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.social-link:hover {
  color: #3b82f6;
  background: #3b82f64d;
  border-color: #3b82f680;
  transform: translateY(-3px)scale(1.1);
}

.social-icon {
  width: 20px;
  height: 20px;
}

@media (width <= 768px) {
  .app-footer {
    margin-top: 60px;
    padding: 30px 0 20px;
  }

  .footer-container {
    padding: 0 16px;
  }

  .footer-main {
    grid-template-columns: 1fr;
    gap: 30px;
    margin-bottom: 30px;
  }

  .footer-section {
    text-align: center;
  }

  .footer-title {
    margin-bottom: 12px;
    font-size: 1.3rem;
  }

  .footer-description {
    font-size: .95rem;
  }

  .footer-subtitle {
    margin-bottom: 12px;
    font-size: 1rem;
  }

  .language-selector {
    justify-content: center;
    display: flex;
  }

  .language-btn {
    padding: 10px 16px;
    font-size: .9rem;
  }

  .language-dropdown {
    min-width: 200px;
    left: 50%;
    transform: translateX(-50%)translateY(-10px);
  }

  .language-dropdown.show {
    transform: translateX(-50%)translateY(0);
  }

  .footer-bottom {
    text-align: center;
    flex-direction: column;
    gap: 20px;
  }

  .footer-social {
    justify-content: center;
  }

  .footer-info {
    order: 2;
  }

  .footer-social {
    order: 1;
  }

  .social-link {
    width: 36px;
    height: 36px;
  }

  .social-icon {
    width: 18px;
    height: 18px;
  }
}

@media (width <= 480px) {
  .app-footer {
    margin-top: 50px;
    padding: 25px 0 15px;
  }

  .footer-container {
    padding: 0 12px;
  }

  .footer-main {
    gap: 25px;
    margin-bottom: 25px;
  }

  .footer-title {
    font-size: 1.2rem;
  }

  .footer-description {
    font-size: .9rem;
  }

  .language-btn {
    padding: 8px 14px;
    font-size: .85rem;
  }

  .language-dropdown {
    min-width: 180px;
  }

  .footer-bottom {
    gap: 15px;
  }

  .footer-social {
    justify-content: center;
  }

  .social-link {
    width: 36px;
    height: 36px;
  }

  .social-icon {
    width: 18px;
    height: 18px;
  }

  .footer-text {
    font-size: .85rem;
  }

  .footer-version {
    font-size: .75rem;
  }
}

@media (width >= 769px) and (width <= 1024px) {
  .footer-main {
    grid-template-columns: 1.5fr 1fr 1fr;
    gap: 30px;
  }

  .footer-container {
    padding: 0 24px;
  }
}

@media (prefers-color-scheme: dark) {
  .app-footer {
    background: linear-gradient(135deg, #0a0e14fa 0%, #141c24f2 100%);
  }

  .language-dropdown {
    background: #0a0e14fa;
  }
}

@media (prefers-contrast: high) {
  .app-footer {
    border-top: 2px solid #fff;
  }

  .footer-link:hover {
    text-decoration: underline;
  }

  .social-link {
    border: 2px solid #fff;
  }
}

@media (prefers-reduced-motion: reduce) {
  .language-btn, .language-option, .footer-link, .social-link {
    transition: none;
  }

  .language-dropdown {
    transition: opacity .2s;
  }
}
